body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  max-width: 468px;
  min-width: 362px;
  height: 100vh;
  padding: 0;
  margin: 0 auto;
  background-color: #fff;
  font-family:  'Comfortaa', cursive, sans-serif;
}
a{
  text-decoration: none;
}
#root{
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.container {
  width: 100%;
  height: 100%;
  background-color: #253A57;
  /*border: 5px solid #253A57;*/
  border-radius: 40px;
  overflow: hidden;
}

.btn{
  width: 250px;
  height: 75px;
}




.content{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  
}
.content span{
  color: white;
}

.categories{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  
}
.cat_item{
  width: 150px;
  height: 150px;
  overflow: hidden;
  padding: 5px;
  border: 3px solid white;
  border-radius: 10px;
  margin: 10px 10px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;    
  
}
.cat_item:hover{
  background-color: #23527E;
  cursor: pointer;
}
.cat_item p{
  color: #fff;
  font-size: 100%;
  text-align: center;
}
.cat_item_icon{
  display: none;
  width: 70%;
  height: 70%;
  margin-bottom: 10px;
  border-radius: 50%;
  background-color: tomato;
}






@media screen and (max-width: 412px) {
  body{
      max-width: 100vw;
      
  }
  .container{
      border-radius: 0;
  }
}
